/* 02 - PRELOADER SETUP:; */
/* ===================================*/



#preloader  {
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   background-color: #eee;
   z-index: 9999;
  height: 100%;
  width: 100%;
}

#status  {
   width: 100px;
   height: 100px;
   border-radius: 100px;
   position: absolute;
   left: 50%;
   top: 50%;
   background-color: #fafafa;
   /* background-image: url(./images/loader.gif); */
