/*HOME*/

#home {
  background:#fff;
  background-size: cover;
  min-height: 500px;
  padding-top: 120px;
  padding-bottom: 0px;
}


#welcome{
 /* background: url('./images/tiles/05a.jpg') center center repeat; */
  background-size: cover;
}
#welcome h1{
  color: #121212;
}
#welcome h3{
  padding: 10px;
  background: #EB4B27;
  color: #fff;
  margin-top: 20px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 100;
  font-size: 18px;
  text-transform: uppercase;
}
.ticker-wrap{
  margin-top: 0px;
}
#news_ticker {
height: 240px;
overflow: hidden;
padding: 3px;
position: relative;
}
.ticker-list{
float: left;
padding: 0px;
list-style: none;
-webkit-animation: ticker 10s cubic-bezier(1, 0, .5, 0) infinite;
-moz-animation: ticker 10s cubic-bezier(1, 0, .5, 0) infinite;
-ms-animation: ticker 10s cubic-bezier(1, 0, .5, 0) infinite;
animation: ticker 10s cubic-bezier(1, 0, .5, 0) infinite;
}
.ticker-list:hover {
-webkit-animation-play-state: paused;
-moz-animation-play-state: paused;
-ms-animation-play-state: paused;
animation-play-state: paused;
}
.ticker-list li {line-height: 220px;}
#news_ticker a {
color: #121212;
text-decoration: none;
font-size: 130px;
}

@-webkit-keyframes ticker {
0% {margin-top: 0;}
25% {margin-top: -260px;}
50% {margin-top: -520px;}
75% {margin-top: -780px;}
100% {margin-top: 0;}
}
@-moz-keyframes ticker {
0% {margin-top: 0;}
25% {margin-top: -260px;}
50% {margin-top: -520px;}
75% {margin-top: -780px;}
100% {margin-top: 0;}
}
@-ms-keyframes ticker {
0% {margin-top: 0;}
25% {margin-top: -260px;}
50% {margin-top: -520px;}
75% {margin-top: -780px;}
100% {margin-top: 0;}
}
@keyframes ticker {
0% {margin-top: 0;}
25% {margin-top: -260px;}
50% {margin-top: -520px;}
75% {margin-top: -780px;}
100% {margin-top: 0;}
}

