  color: #686351;
}
/*-- 1. General --*/
div#portfolio {
  padding-top: 50px;
  background: #333;
}

div#portfolio_title {
  margin-bottom: 33px;
}

div#portfolio_title h2 {
  color: #333;
}

.sortablePortfolio, #portfolioFilter {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

#portfolio-wrap {
  padding-top: 20px;
}

/*-- 2. Filter --*/
div#filter {

}

div#filter_wrapper {
  float: left;
  position: relative;
  margin-bottom: 0px;
  left: 50%
}

ul#portfolioFilter {
  line-height: 18px;
  float: left;
  position: relative;
  left: -50%;
}

ul#portfolioFilter li {
  float: left;
  height: 46px;
  line-height: 46px;
  padding-right: 10px;
  padding-left: 10px;
}

ul#portfolioFilter li.separator {
  line-height: 49px;
  padding: 0px;
  color: #CCC;
  font-size: 11px;
}

ul#portfolioFilter li a {
  font-size: 14px;
  color: #333;
  font-family:'Montserrat', sans-serif;
  
}


/*-- 3. Thumbs --*/
div#portfolio_thumbs {
  position: relative;
  z-index: 10;
}

div#portfolio_thumbs ul {
  display: block;
}

div#portfolio_thumbs ul li {
  float: left;
  width: 33.33%;
  position: relative;
  margin-bottom: 20px;
  padding: 10px;
  -moz-transition: all 1.25s ease;
  -webkit-transition: all 1.25s ease;
  -o-transition: all 1.25s ease;
  transition: all 1.25s ease;
}
div#portfolio_thumbs ul li:hover {
  -moz-transition: all 1.25s ease;
  -webkit-transition: all 1.25s ease;
  -o-transition: all 1.25s ease;
  transition: all 1.25s ease;

}

ul#portfolioFilter .current {color:#eeabcc}

div#portfolio_thumbs ul li img {
  width: 100%;
  height: auto;
  display: block;

}

div#portfolio_thumbs ul li img.rollover {
  position: absolute;
  opacity: 0;
  filter: alpha(opacity = 0);
  top: 0px;
  left: 0px;
}

div#portfolio_thumbs ul li div.item_info {
  display: none;
  position: absolute;
  top: 30%;
  left: 0px;
  z-index: 11;
  width: 100%;
  padding-top: 25px;
  padding-bottom: 15px;
  text-align: center;
}

div#portfolio_thumbs ul li div.item_info h3 {
  font-weight: normal;
  margin-bottom: 8px;
}

div#portfolio_thumbs ul li div.item_info h3 a {
  color: #FFF;
}
div#portfolio_thumbs ul li div.item_info h3 a > span{
  padding: 4px 8px;
  font-size: 14px;
  line-height: 25px;
  color: #FFF;
  font-family:'Montserrat', sans-serif;
  
}
div#portfolio_thumbs ul li div.item_info p > span{
  padding: 4px 8px;
  font-size: 14px;
  line-height: 21px;
  color: #fff;
  font-family: 'Open Sans', sans-serif;
  
  background: #000;
}


/*-- 4. Item page --*/
