/* Slider */
#item_slider {
/*    width: 680px;*/
  margin: 0;
  padding: 0;
  margin-top: 20px;
}

#item_slider a:active {
  outline: none;
}

.slides,  .flex-control-nav,  .flex-direction-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}

#item_slider .slides > li {
  display: none;
}

#item_slider .slides img {
  max-width: 100%;
  display: block;
}

#item_slider {
  position: relative;
  zoom: 1;
}

#item_slider .slides {
  zoom: 1;
}

#item_slider .slides > li {
  position: relative;
}

#item_slider .flex-direction-nav li a {
  width: 30px;
  height: 30px;
  line-height: 29px;
  border-radius: 30px;
  background: #493D1B;
  position: absolute;
  top: 165px;
  cursor: pointer;
  text-indent: 1px;
  font-size: 15px;
  font-family: 'Open Sans', sans-serif;
  
  text-align: center;
}

#item_slider .flex-direction-nav li .porto {
  right: 10px;
  color: #FFF;
}

#item_slider .flex-direction-nav li .prev {
  left: 10px;
  color: #FFF;
}

#item_slider .flex-direction-nav li .porto:hover {
  background: #000;
  color: #fff;
}

#item_slider .flex-direction-nav li .prev:hover {
  background: #000;
  color: #fff;
}

